export default `

.root {
  background-color: #FFF;
  padding: 10px;
  color: #444;
  border-radius: 3px;
  font-size: 16px;
  min-height: 38px;
  box-shadow: 1px 1px 4px rgba(0,0,0,0.3);
}

input {
  height: 100%;
  width: 100%;
  font-size: 16px;
  background-color: rgba(0,0,0,0);
}
`



